import Geom from './Geom';
import mode from '../mode';
import gradient from '../style/gradient';

const OFFSET = Math.PI * 0.5;

function getCoordsByDegree(x, y, r, d) {
  d = d % 360;
  if(d >= 0 && d < 90) {
    return [
      x + Math.sin(d * Math.PI / 180) * r,
      y - Math.cos(d * Math.PI / 180) * r
    ];
  }
  else if(d >= 90 && d < 180) {
    return [
      x + Math.cos((d - 90) * Math.PI / 180) * r,
      y + Math.sin((d - 90) * Math.PI / 180) * r,
    ];
  }
  else if(d >= 180 && d < 270) {
    return [
      x - Math.cos((270 - d) * Math.PI / 180) * r,
      y + Math.sin((270 - d) * Math.PI / 180) * r,
    ];
  }
  else {
    return [
      x - Math.sin((360 - d) * Math.PI / 180) * r,
      y - Math.cos((360 - d) * Math.PI / 180) * r,
    ];
  }
}

class Sector extends Geom {
  constructor(tagName, props) {
    super(tagName, props);
    // 角度
    this.__begin = 0;
    this.__end = 0;
    if(this.props.begin) {
      this.__begin = parseFloat(this.props.begin);
      if(isNaN(this.begin)) {
        this.__begin = 0;
      }
    }
    if(this.props.end) {
      this.__end = parseFloat(this.props.end);
      if(isNaN(this.end)) {
        this.__end = 0;
      }
    }
    // 半径0~1，默认1
    this.__r = 1;
    if(this.props.r) {
      this.__r = parseFloat(this.props.r);
      if(isNaN(this.r)) {
        this.__r = 1;
      }
    }
    // 扇形两侧是否有边
    this.__edge = false;
    if(this.props.edge !== undefined) {
      this.__edge = !!this.props.edge;
    }
  }

  render(renderMode) {
    super.render(renderMode);
    let { rx: x, ry: y, width, height, mlw, mtw, plw, ptw, style, ctx, begin, end, r, virtualDom } = this;
    if(begin === end) {
      return;
    }
    let {
      cx, cy, display, fill,
      stroke, strokeWidth, strokeDasharray,
      slg, flg, frg } = this.__getPreRender();
    if(display === 'none') {
      return;
    }
    r *= Math.min(width, height) * 0.5;
    if(renderMode === mode.CANVAS) {
      ctx.strokeStyle = slg ? gradient.createCanvasLg(ctx, slg) : stroke;
      ctx.lineWidth = strokeWidth;
      if(flg) {
        ctx.fillStyle = gradient.createCanvasLg(ctx, flg);
      }
      else if(frg) {
        ctx.fillStyle = gradient.createCanvasRg(ctx, frg);
      }
      else {
        ctx.fillStyle = fill;
      }
      ctx.setLineDash(strokeDasharray);
      ctx.beginPath();
      if(this.edge) {
        ctx.moveTo(cx, cy);
      }
      ctx.arc(cx, cy, r, begin * Math.PI / 180 - OFFSET, end * Math.PI / 180 - OFFSET);
      if(this.edge) {
        ctx.lineTo(cx, cy);
      }
      ctx.fill();
      ctx.stroke();
      ctx.closePath();
    }
    else if(renderMode === mode.SVG) {
      let x1, y1, x2, y2;
      [ x1, y1 ] = getCoordsByDegree(cx, cy, r, begin);
      [ x2, y2 ] = getCoordsByDegree(cx, cy, r, end);
      let large = (end - begin) > 180 ? 1 : 0;
      if(slg) {
        let uuid = gradient.createSvgLg(this.defs, slg);
        stroke = `url(#${uuid})`;
      }
      if(flg) {
        let uuid = gradient.createSvgLg(this.defs, flg);
        fill = `url(#${uuid})`;
      }
      else if(frg) {
        let uuid = gradient.createSvgRg(this.defs, frg);
        fill = `url(#${uuid})`;
      }
      let d;
      if(this.edge) {
        d = `M${cx} ${cy} L${x1} ${y1} A${r} ${r} 0 ${large} 1 ${x2} ${y2} z`;
      }
      else {
        d = `M${x1} ${y1} A${r} ${r} 0 ${large} 1 ${x2} ${y2}`;
      }
      this.addGeom('path', [
        ['d', d],
        ['fill', fill],
        ['stroke', stroke],
        ['stroke-width', strokeWidth],
        ['stroke-dasharray', strokeDasharray]
      ]);
    }
  }

  get begin() {
    return this.__begin;
  }
  get end() {
    return this.__end;
  }
  get r() {
    return this.__r;
  }
  get edge() {
    return this.__edge;
  }
}

export default Sector;
